<template>
	<div id="mealRedpacket-list">
		<public-header title="红包列表"></public-header>
		<div class="content">
			<div class="redpacket-list" v-if="records.data.length > 0">
				<div class="redpacket-item margin-10-b" v-for="(item, index) in records.data" >
					<div class="redpacket-container">
						<div class="flex-lr c-danger">
							<div>{{item.name}}</div>
							<div class="price"><span class="font-16">￥</span>{{item.discount}}.00</div>
						</div>
						<div class="flex-lr redPacket-use-limit">
							<div class="flex">
								<div class="itag itag-danger" v-if="records.type == 'exchangeRedpacket'">套餐红包</div>
								<div class="itag itag-danger" v-else-if="records.type == 'mealRedpacket'">套餐红包Plus</div>
								<div class="c-gray font-12">有效期{{item.use_days_limit}}天</div>
							</div>
							<div class="c-gray font-12">满{{item.condition}}.00元可用</div>
						</div>
					</div>
					<span class="border-1px-r circle circle-left"></span>
					<span class="border-1px-l circle circle-right"></span>
				</div>
			</div>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				id: 0,
				records: {
					type: '',
					data: []
				},
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					data: {
						id: that.id
					},
					url: 'plateform/mealRedpacket/list',
					success: (res) => {
						that.records.data = [...that.records.data, ...res.records.data.meal.data];
						that.records.type = res.records.type;
					}
				});
			},
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
   #mealRedpacket-list .content .redpacket-list{
		margin: 10px;
   }
   #mealRedpacket-list .content .redpacket-list .redpacket-item{
		background: #fff;
		margin-bottom: 10px;
		overflow: hidden;
		position: relative;
   }
   #mealRedpacket-list .content .redpacket-list .redpacket-item .redpacket-container{
		padding: 15px;
   }
   #mealRedpacket-list .content .redpacket-list .redpacket-item .redpacket-container .price{
		font-size: 20px;
   }
   #mealRedpacket-list .content .redpacket-list .redpacket-item .redpacket-container .redPacket-use-limit{
		padding: 10px 0;
		color: #666;
		font-size: 12px;
   }
   #mealRedpacket-list .content .redpacket-list .redpacket-item .redpacket-container .redPacket-use-limit .itag{
	   font-size: 12px;
	   padding: 2px 4px;
	   margin-right: 10px;
   }
   #mealRedpacket-list .content .redpacket-list .redpacket-item .circle{
	   display: block;
	   width: 16px;
	   height: 16px;
	   background: #eee;
	   position: absolute;
	   border-radius: 16px;
	   top: 50%;
	   margin-top: -8px;
	   z-index: 20;
   }
   #mealRedpacket-list .content .redpacket-list .redpacket-item .circle-left{
	   left: -8px;
   }
   #mealRedpacket-list .content .redpacket-list .redpacket-item .circle-right{
	   right: -8px;
   }
</style>


